import React, { useState } from "react";
import styled from "styled-components";

const countries = [
    {
    countryCode: 'kr',
    countryName: '한국',
    },
    {
    countryCode: 'cu',
    countryName: '쿠바',
    },
    {
    countryCode: 'hk',
    countryName: '홍콩',
    },
    {
    countryCode: 'jp',
    countryName: '일본',
    },
    {
    countryCode: 'nz',
    countryName: '뉴질랜드',
    },
];

const ContrySet = styled.div`
`;

const ContryBtn = (props) => {
    const [Country, setCountry] = useState('kr');
    const handleChange = (e) => {
        setCountry(e.target.value);
    };
    return(
            <ContrySet country = {Country}>
                <select onChange={handleChange} >
                    {countries.map((c) => (
                        <option
                            name={c.countryName}
                            value={c.countryCode}
                            defaultValue={props.defaultValue === c.countryCode}
                        >
                        {c.countryName}
                        </option>
                    ))}
                </select>
            </ContrySet>
    );
};

export default ContryBtn;