import React  from 'react';
import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

const categories = [
  {
    name: 'all',
    text: '전체보기',
  },
  {
    name: 'business',
    text: '비지니스',
  },
  {
    name: 'entertainment',
    text: '엔터테인먼트',
  },
  {
    name: 'health',
    text: '건강',
  },
  {
    name: 'science',
    text: '과학',
  },
  {
    name: 'sports',
    text: '스포츠',
  },
  {
    name: 'technology',
    text: '기술',
  },
];
//상단 헤더바
const CategoriesBlock = styled.div`
  position: fixed;
  padding: 1rem;
  top: 0;
  box-sizing: border-box;
  text-align : center;
  width: 100%;
  justify-content : center;//display가 flex가 되어있다면 이것으로 정렬
  background-color: white;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  @media screen and (max-width: 768px) {
    width: 100%;
    overflow-x: auto;
  }
`;
const Category = styled(NavLink)`
  font-size: 1.125rem;
  font-weight: 1000;
  cursor: pointer;
  white-space: pre;
  text-decoration: pre;
  text-decoration: none;
  color: inherit;
  text-align: center;
  padding-bottom: 0.25rem;
  &:hover {
    color: #50AF49;
  }

  &.active {
    font-weight: 600;
    border-bottom: 2px solid #50AF49;
    color: #50AF49;
    &:hover {
      color: #50AF49;
    }
  }

  & + & {
    margin-left: 1rem;
  }
`;



const Categories = () => {
  return (
    <>
    <CategoriesBlock>
      {categories.map((c) => (
        <Category
          key={c.name}
          className={({isActive}) => ( isActive ? "active" : "")}
          to={c.name === 'all' ? '/' : `/${c.name}`}
        >
          {c.text}
        </Category>
      ))}
    </CategoriesBlock>
    
    </>
  );
};

export default Categories;

